/* Traditional/Legacy styles */

/* Overwriting bootstrap */
.navbar {
    padding: 0px
}

.nav-icons-wrapper {
    height:20px!important;
    width: 20px!important;
    margin: auto;
}
.nav-icons-wrapper div {
    position: relative;
    top: 1px;
    height: 1px;
    width: 17px;
    margin: 5px auto 0 auto;
    background-color: #dcd6ce;
}
.carouselImage {
    max-height: 400px;
    width: auto!important;
}

.carouselColumn {
    max-width: 600px;
}


/* Make carousel icons bigger */
.carouselColumn button.control-arrow::before {
    border-top: 20px solid transparent!important;
    border-bottom: 20px solid transparent!important;

}
.carouselColumn button.control-next::before {
    border-left: 20px solid #fff!important;
}
.carouselColumn button.control-prev::before {
    border-right: 20px solid #fff!important;
}
/* 
Defines global variables
e.g. colour schemes and fonts
 */


/* Visited hyperlinks: #a22e2e */
/* Non-visited hyperlinks: #6385a9 */
:root {
  --pageBackgroundColor: #0f0f0f;  
  --textColor: #dcd6ce;


  /* Paragraphs */
  --pFontFamily: 'Roboto Slab', serif;

  /* Navbar */
  --navbarFontFamily: 'Roboto', Helvetica, sans-serif;
  --navbarFontWeight: 100;
  --navbarBackgroundColor: var(--pageBackgroundColor);
}

/*
 Global styles 
 */

/* Global variables (colour schemes, fonts, etc.) */

@media only screen and (min-width: 1000px) {
  .mainInfo {
    right: -170px;
    z-index: 2;
  }
}

body {
    font-family: var(--pFontFamily);
    color: var(--textColor);
    background-color: var(--pageBackgroundColor);
}

a {
  color: #6385a9
}

a:hover {
  color: #2279d6
}

.icon {
  font-size: 33px;
  padding: 0 4px;
  height: 100%;
}

.iconWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.iconWrapper2 {
    background-color: #2a2a2a;
    height: 100%;
    border-radius: 5px;
    padding: 0 5px 0 0;
}

.iconWrapper2 a{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: row;
          flex-direction: row;
  }
  

.fadingElement {
  width: 300px;
  position: absolute;
  height: 100%;
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#0f0f0f+0,000000+100&1+13,1+13,0.84+51,0.03+98 */ /* FF3.6-15 */
